.login-page {
  height: calc(100vh);
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ebebeb;
  background-image: url(data:image/png;base64,);
}
.login-card {
  text-align: center;
  width: calc(50vh);
  max-width: 400px;
  opacity: 0.75;
  border-radius: 25px;
  background-color: #f2f580;
}
.login-card .ant-card-body {
  padding-bottom: 0;
}
